@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap);
/*@import url('https://fonts.googleapis.com/css2?family=Barlow:wght@400;600;700&display=swap');*/

body {
  all: unset;
/*  font-family: 'Barlow', sans-serif;*/
  font-family: 'Poppins', sans-serif;
  margin: 0;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  --window-height: calc(100vh - 2rem);
}

code {
  white-space: pre;
}
.room{position:absolute;-webkit-user-select:none;-ms-user-select:none;user-select:none;background:#ccc;display:grid;grid-template-rows:-webkit-min-content 2fr;grid-template-rows:min-content 2fr;grid-auto-rows:1fr;overflow:hidden;grid-template-areas:"controls" "status"}.picker .room{position:static;min-height:unset;border-radius:4px}.room.free{background:#93E0FF}.room.busy{background:#C80037}.room .controls{background:#fff;grid-area:controls;display:grid;grid-template-columns:1fr;grid-template-rows:1rem;padding:.5rem;line-height:1rem;border-bottom:1px solid rgba(0,0,0,0.25)}.room.active{box-shadow:0 0 0 0.125rem yellow}.room.open .controls{grid-template-columns:-webkit-max-content 1fr;grid-template-columns:max-content 1fr}.room.open .controls .button{overflow:hidden;display:block;width:1rem;margin-right:0.5rem}.room.open .controls .button svg{width:1rem}.room .status{display:grid;place-items:center;grid-area:status;font-size:3rem;color:#fff;font-weight:600}.room .aspects{fill:#fff;display:grid;grid-auto-flow:column;grid-auto-columns:1fr;padding:1rem;place-items:center}.room .aspects svg{max-width:8rem;max-height:8rem}

.picker{background:black;box-sizing:border-box;min-height:100vh;display:grid;grid-template-rows:1fr;grid-template-columns:1fr 2rem;grid-template-areas:"tiles controls"}.picker .pickerControls{fill:#fff;grid-area:controls;padding:1rem 0.5rem;background:rgba(255,255,255,0.2);display:flex;justify-content:flex-end;align-items:flex-start}.picker .pickerControls .button{flex-basis:1rem}.picker .rooms{grid-area:tiles;display:grid;grid-template-columns:repeat(auto-fit, minmax(10rem, 1fr));grid-gap:1rem;gap:1rem;padding:1rem;overflow-y:auto}

.overlay{display:grid;position:absolute;left:0;right:0;top:0;bottom:0;background:rgba(0,0,0,0.5);place-items:center}.overlay .modal{all:unset;background:#fff;width:-webkit-max-content;width:-moz-max-content;width:max-content;height:-webkit-max-content;height:-moz-max-content;height:max-content;display:grid;grid-template-rows:-webkit-max-content 1fr;grid-template-rows:max-content 1fr;grid-template-areas:"header" "content";border-radius:0.25rem}.overlay .modal header{grid-area:header;display:grid;grid-template-columns:1fr 1rem;grid-template-areas:"title close";grid-gap:1.5rem;gap:1.5rem;padding:0.75rem}.overlay .modal header .button.close{grid-area:close}.overlay .modal header hgroup{grid-area:title}.overlay .modal header hgroup h1,.overlay .modal header hgroup h2,.overlay .modal header hgroup h3,.overlay .modal header hgroup h4,.overlay .modal header hgroup h5,.overlay .modal header hgroup h6{all:unset}.overlay .modal main{grid-area:content;padding:0;padding-bottom:0.75rem}.overlay .modal main table{border-collapse:collapse;overflow:hidden}.overlay .modal main table .button.removeRoom{display:grid;align-items:center}.overlay .modal main table .button.removeRoom svg{width:1rem;height:1rem}.overlay .modal main table tr:before,.overlay .modal main table tr:after{content:" ";display:table-cell;width:0.5rem}.overlay .modal main table tr td:hover,.overlay .modal main table tr th:hover{background:rgba(0,0,0,0.05);position:relative}.overlay .modal main table tr td:hover::after,.overlay .modal main table tr th:hover::after{content:"";position:absolute;background:rgba(0,0,0,0.1);left:0;top:-5000px;height:10000px;width:100%;pointer-events:none}.overlay .modal main table .nohover:hover{background:inherit;position:static;position:initial}.overlay .modal main table .nohover td:hover,.overlay .modal main table .nohover th:hover{background:transparent none repeat 0 0 / auto auto padding-box border-box scroll;background:initial;position:static;position:initial}.overlay .modal main table .nohover td:hover::after,.overlay .modal main table .nohover th:hover::after{display:none}.overlay .modal main table tr.addrow{cursor:pointer;font-size:0.75em;color:#066}.overlay .modal main table tr.addrow:hover{background:rgba(0,255,255,0.25)}.overlay .modal main table thead{font-weight:600;border:none;background:black;color:white;border-spacing:0}.overlay .modal main table thead tr td:hover,.overlay .modal main table thead tr th:hover{background:rgba(255,255,255,0.2)}.overlay .modal main table tbody th{text-align:left}.overlay .modal main table tbody tr{border-bottom:1px solid black}.overlay .modal main table tbody tr:hover{background:rgba(0,0,0,0.1)}.overlay .modal main table .textcell{padding:0.25rem 0.5rem}.overlay .modal main table .inputcell{padding:0;display:grid;grid-template-columns:1fr -webkit-max-content;grid-template-columns:1fr max-content;grid-template-rows:-webkit-max-content -webkit-max-content;grid-template-rows:max-content max-content;grid-template-areas:"field processing" "helper helper"}.overlay .modal main table .inputcell input{grid-area:field}.overlay .modal main table .inputcell .error{grid-area:helper;color:red;font-size:0.75em}.overlay .modal main table .inputcell .spinner{grid-area:processing}.overlay .modal main table input[type="text"]{all:unset;padding:0.25rem 0.5rem;background:rgba(0,0,0,0.05);box-shadow:inset 0 0 0.25rem rgba(0,0,0,0.25)}.overlay .modal main table input[type="text"]:focus{background:rgba(0,0,0,0.1);box-shadow:inset 0 0 0.25rem rgba(0,0,0,0.5)}

.RoomMonitor{height:var(--window-height);display:grid;grid-template-rows:1fr;margin:1rem;position:relative}svg{width:100%;height:100%}.open.room{opacity:1;top:0;left:0;min-height:100vh;min-width:100vw;max-height:100vh;max-width:100vw;height:100vh;width:100vw}.open.room-enter{opacity:0;left:var(--startX);top:var(--startY);min-width:var(--startWidth);min-height:var(--startHeight);max-width:var(--startWidth);max-height:var(--startHeight);width:var(--startWidth);height:var(--startHeight);border-radius:4px}.open.room-enter .controls .button{width:0;margin-right:0}.open.room-enter-active{opacity:1;top:0;left:0;min-height:100vh;min-width:100vw;max-height:100vh;max-width:100vw;height:100vh;width:100vw;transition:opacity 50ms ease-in-out,  top 300ms ease-in-out, left 300ms ease-in-out, min-width 300ms ease-in-out, min-height 300ms ease-in-out, max-width 300ms ease-in-out, max-height 300ms ease-in-out, width 300ms ease-in-out, height 300ms ease-in-out, border-radius 300ms ease-in-out;border-radius:0}.open.room-enter-active .controls .button{width:1rem;margin-right:0.5rem;transition:width 300ms ease-in-out, margin-right 300ms ease-in-out}.open.room-enter-done{opacity:1;top:0;left:0;min-height:100vh;min-width:100vw;max-height:100vh;max-width:100vw;height:100vh;width:100vw;border-radius:0}.open.room-enter-done .controls .button{width:1rem;margin-right:0.5rem}.open.room-exit{opacity:1;top:0;left:0;min-height:100vh;min-width:100vw;max-height:100vh;max-width:100vw;height:100vh;width:100vw;border-radius:0}.open.room-exit .controls .button{width:1rem;margin-right:0.5rem}.open.room-exit-active{opacity:0;border-radius:0.25rem;left:var(--startX);top:var(--startY);min-width:var(--startWidth);min-height:var(--startHeight);max-width:var(--startWidth);max-height:var(--startHeight);width:var(--startWidth);height:var(--startHeight);transition:opacity 50ms ease-in-out 250ms,  top 300ms ease-in-out, left 300ms ease-in-out, min-width 300ms ease-in-out, min-height 300ms ease-in-out, max-width 300ms ease-in-out, max-height 300ms ease-in-out, width 300ms ease-in-out, height 300ms ease-in-out, border-radius 300ms ease-in-out}.open.room-exit-active .controls .button{width:0;margin-right:0;transition:width 300ms ease-in-out, margin-right 300ms ease-in-out}

